
body{
	margin: 0;
	padding: 0;
}
#app {
  width: 100%;
  height: 100%;
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  z-index: 0;
}
/**
 * 去掉列表的默认样式
 */
ul,li{
  list-style: none;
  margin: 0;
  padding: 0;
}
/**
 * 滚动列表
 */
.wrapper{
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  position: relative;
  overflow: hidden;
}
/**
 * 边距
 */
.mt1{
  margin-top: 0.5em;
}
.detail-nav{
	background-color: #FFFFFF;
	border: 1px #DDDDDD solid;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  padding-left: 0.5em;
}
.detail-nav::before{
  content: "< ";
}

.list-item{
		width: 100%;
		height: auto;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
}
.list-item .cover,.list-item .cover img{
		width: 100px;
		height: auto;
}

.detail-cover{
  width: 100%;
  height: 280px;
  text-align: center;
}
.detail-cover img{
  width: auto;
  height: 100%;
}
.watch-list{
}
.watch-list .list-item{
  width: 100%;
  height: 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 40px;
}

.navbar{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	background-color: #FFFFFF;
}
.nav{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	text-align: center;
	padding-top: 1em;
	padding-bottom: 1em;
	border: 1px #DDDDDD solid;
}
.navbar .active{
	background-color: #dde7e8 !important;
}

